import { Facebook, LinkedIn } from "@material-ui/icons";
import styled from "styled-components";
import { mobile } from "../pages/responsive";

const Container = styled.div`
  display: flex;
  float: left;
  ${mobile({ flexDirection: "column" })}
`;

const Left = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
`;

const Desc = styled.p`
  margin: 10px 0px;
`;

const SocialContainer = styled.div`
  display: flex;
`;

const SocialIcon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 2%;
  color: white;
  background-color: #${(props) => props.color};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
`;

const Center = styled.div`
  flex: 1;
  padding: 5px;
  ${mobile({ display: "none" })}
`;

const Title = styled.h3`
  margin-bottom: 5px;
`;

const List = styled.ul`
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
`;

const ListItem = styled.li`
  width: 50%;
  margin-bottom: 5px;
`;

const Right = styled.div`
  flex: 1;
  padding: 20px;
  ${mobile({ backgroundColor: "#fff8f8" })}
`;

const ContactItem = styled.div`
  margin-bottom: 1px;
  display: flex;
  align-items: center;
`;

const Footer = () => {
  return (
    <Container>
      <Left>
        <Title>ABOUT US</Title>
        <Desc>
          Rubixe™ is a global technology company specializing in disruptive
          technologies – Artificial Intelligence (AI), Machine Learning, Robotic
          Process Automation (RPA), BlockChain and Internet of Things (IoT).
          Rubixe mission to enable businesses to leverage the full potential of
          disruptive technologies to stay competitive in the market.
        </Desc>
      </Left>
      <Center>
        <Title>MENU</Title>
        <List>
          <ListItem>Home</ListItem>
          <ListItem>Services</ListItem>
          <ListItem>Products</ListItem>
          <ListItem>Career</ListItem>
        </List>
      </Center>
      <Right>
        <Title>ADDRESS</Title>
        <ContactItem>
          Novel Tech Park, 1st Floor, Hosur Rd, Kudlu gate, Bengaluru, Karnataka
          560068
        </ContactItem>
        <ContactItem>Phone: 0804-717-8999</ContactItem>
        <ContactItem>Email: hi@rubixe.com</ContactItem>
        <SocialContainer>
          <Title>SOCIAL MEDIA</Title>
          <SocialIcon color="3B5999">
            <Facebook />
          </SocialIcon>
          <SocialIcon color="E4405F">
            <LinkedIn />
          </SocialIcon>
        </SocialContainer>
      </Right>
    </Container>
  );
};

export default Footer;
