import React from "react";
import "../App.css";

const RegistrationForm = () => {
  return (
    <div className="reg">
      <div className="contact contact-us-two border-primary">
        <div className="col-sm-12 col-xs-12 text-center mb20">
          <h4 className="pb25 bb-solid-1 text-uppercase">
            Get in Touch
            <small className="text-lowercase">
              Please complete the form and we will get back to you.
            </small>
          </h4>
        </div>

        <form
          name=""
          id="idForm"
          method="post"
          action="https://rubixe.com/mail-send.php"
        >
          <div className="form-group col-md-6 col-sm-6 col-xs-12">
            <input
              type="text"
              name="senderName"
              id="senderName"
              class="input-md input-rounded form-control"
              placeholder="fullname"
              maxlength="100"
            />
          </div>

          <div className="form-group col-md-6 col-sm-6 col-xs-12">
            <input
              type="email"
              name="senderEmail"
              id="senderEmail"
              class="input-md input-rounded form-control"
              placeholder="email address"
              maxlength="100"
            />
          </div>

          <div class="form-group col-md-6 col-sm-6 col-xs-12">
            <input
              type="number"
              name="mobileNumber"
              id="mobileNumber"
              class="input-md input-rounded form-control"
              maxlength="100"
              placeholder="Mobile Number"
            />
          </div>

          <div class="form-group col-md-12 col-sm-12 col-xs-12">
            <button
              name="sendMessage"
              id="sendMessage"
              class="button button-md button-block button-grad-stellar"
            >
              Registration Now
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};
export default RegistrationForm;
